'use client'

import { Swiper, SwiperSlide } from 'swiper/react'; 
import SwiperCore, { Navigation, Autoplay } from 'swiper'; 
import 'swiper/swiper-bundle.css'; 
import Link from 'next/link';

SwiperCore.use([Navigation, Autoplay]);

export default function MainDashboard({ featuredMovies, filterMovies, genreFilters }) {
  return (
    <div className="p-8 text-white bg-black">
      {/* Carrusel de fondo de películas */}
      <div className="relative">
        <Swiper
          spaceBetween={20}
          slidesPerView={3}
          autoplay={{ delay: 5000 }}
          navigation={true} 
          modules={[Navigation, Autoplay]} 
          className="h-full"
        >
          {featuredMovies.map((movie) => (
            <SwiperSlide key={movie.id}>
              <Link href={`/movie/${movie.id}`} passHref>
                <img
                  src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                  alt={movie.title}
                  className="w-full h-full object-cover"
                />
              </Link>
              <div className="absolute bottom-4 left-4 text-white">
                <h3 className="text-2xl font-bold">{movie.title}</h3>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      {/* Carruseles por filtros */}
      <div className="mt-12">
        <div className="space-y-12">
          {genreFilters.map((genre) => (
            <div key={genre.id}>
              <h3 className="text-xl font-semibold">{genre.name}</h3>
              <Swiper
                spaceBetween={20}
                slidesPerView={10}
                navigation={true}
                modules={[Navigation]}
                className="mt-6"
                breakpoints={{
                  640: { slidesPerView: 2, spaceBetween: 10 },
                  768: { slidesPerView: 4, spaceBetween: 10 },
                  1024: { slidesPerView: 10, spaceBetween: 20 },
                }}
              >
                {filterMovies[genre.name]?.map((movie) => (
                  <SwiperSlide key={movie.id}>
                    <Link href={`/movie/${movie.id}`} passHref>
                      <img
                        src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
                        alt={movie.title}
                        className="w-full h-full object-cover rounded-lg"
                      />
                    </Link>
                    <div className="mt-2 text-center">
                      <p className="font-semibold">{movie.title}</p>
                      <p className="text-sm">{movie.vote_average} ⭐</p>
                    </div>
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

// Usamos getServerSideProps para obtener los datos del lado del servidor y pasarlos como props
export async function getServerSideProps() {
  const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
  const BASE_URL = 'https://api.themoviedb.org/3';

  // Consolidar todas las peticiones en una función para obtener todos los datos
  const fetchData = async (endpoint) => {
    const response = await fetch(`${BASE_URL}${endpoint}?api_key=${API_KEY}&language=es-ES`);
    const data = await response.json();
    return data;
  };

  try {
    // Obtener las películas populares y géneros
    const [featuredMoviesData, genresData] = await Promise.all([
      fetchData('/movie/popular'),
      fetchData('/genre/movie/list')
    ]);

    const filterMovies = {};
    // Obtener las películas por género de forma paralela
    for (const genre of genresData.genres) {
      const moviesForGenre = await fetchData(`/discover/movie?with_genres=${genre.id}`);
      filterMovies[genre.name] = moviesForGenre.results;
    }

    return {
      props: {
        featuredMovies: featuredMoviesData.results,
        filterMovies,
        genreFilters: genresData.genres,
      },
    };
  } catch (error) {
    console.error('Error fetching data: ', error);
    return {
      props: {
        featuredMovies: [],
        filterMovies: {},
        genreFilters: [],
      },
    };
  }
}
